@import "../../base";

.MovieSlider {
  &__container {
    display: flex;
    align-items: center;
  }
  &__title {
    padding-left: 24px;
    font-size: 1.6rem;
    font-weight: bold;
  }
  &__slider {
    display: flex;
    gap: 24px;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    scroll-behavior: smooth;
  }
  &__arrow {
    min-width: 40px;
    height: 40px;
    cursor: pointer;
    opacity: 0.5;
  }
  ::-webkit-scrollbar {
    display: none;
  }
}

.MovieSlider__arrow:hover {
  opacity: 1;
}

@media (max-width: 768px) {
  .MovieSlider {
    &__arrow {
      display: none;
    }
  }
}
