@import "../../base";

.Header {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100vw;
  padding: 24px;
  background-color: $third-color;
  z-index: 2;
  &__logo {
    display: flex;
    align-items: center;
    .logo__icon {
      width: 40px;
      height: 40px;
      color: $secondary-color;
    }
    .logo__name {
      font-size: 2rem;
      font-weight: bold;
      color: $secondary-color;
    }
  }
  &__routes {
    display: flex;
    font-size: 1.4rem;
    gap: 16px;
    font-weight: bold;
    .routes__route {
      cursor: pointer;
      padding-bottom: 4px;
    }
    .routes__route:nth-child(2) {
      display: flex;
      align-items: center;
      gap: 4px;
      svg {
        transform: rotate(90deg);
        -webkit-transform: rotate(90deg);
        -moz-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        -o-transform: rotate(90deg);
      }
    }
    .routes__route:nth-child(1):hover {
      border-bottom: 2px solid $secondary-color;
    }
    .routes__route.active {
      border-bottom: 2px solid $secondary-color;
    }
  }
  &__form {
    display: flex;
    align-items: center;
    background-color: $primary-color;
    width: 30%;
    @include borderRadius(12px);
  }
  &__input {
    width: 90%;
    border: none;
    outline: none;
    padding: 12px;
    font-family: inherit;
    @include borderRadius(12px);
  }
  &__button {
    display: block;
    width: 10%;
    height: 39px;
    background-color: $primary-color;
    border: none;
    outline: none;
    cursor: pointer;
    @include borderRadius(12px);
    .button__icon {
      width: 20px;
      height: 20px;
    }
  }
  &__login {
    font-size: 1.6rem;
    font-weight: bold;
    background-color: $secondary-color;
    color: $primary-color;
    padding: 12px 16px;
    @include borderRadius(32px);
  }
}

@media (max-width: 767px) {
  .Header {
    justify-content: space-between;
    &__logo,
    &__login {
      display: none;
    }
    &__form {
      width: 50%;
    }
  }
}
@media (max-width: 320px) {
  .Header {
    padding: 12px;
    justify-content: normal;
    &__form {
      width: 100%;
    }
    &__routes {
      gap: 4px;
    }
  }
}
